@use 'base/mixins' as m;
@use 'base/variables' as v;
.acciones{
    margin-top: v.$separacion;
    display: flex;
    flex-direction: column;
    align-items: center; 
    @include m.tablet { 
        flex-direction: row;
        justify-content: space-between;  
    }
    a{
        text-align: center;
        margin-bottom: 3rem;
    }
}