@use 'base/mixins' as m;
@use 'base/variables' as v;
.modal{
    position: absolute;
    background-color: rgba(44, 61, 207, 0.452);
    left: 0;
    top: 0;
    right: 0;
    bottom:0;
    .formulario{
        max-width: 90%;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 500ms;
        transition-timing-function: ease-in-out;
        opacity: 0;
        &.animacion{
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        @include m.tablet { 
            max-width: 60rem;
        }
        &.cerrar{
            transform: translateY(-200%);
            opacity: 0;
        }
        input[type="text"]{
            // border:1px solid v.$indigo;
            border-radius: .5rem;
            box-shadow: 0px 0px 7px 1px v.$indigo;
        }
    }
    .nueva-tarea{
        border-radius: 1rem;
        background-color: v.$blanco;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        .campo{
            display: flex;
            flex-direction: column;
            @include m.tablet { 
                flex-direction: row;
            }
            label{
                flex: none;
                padding: 1rem 0;
                @include m.tablet { 
                    flex: 0 0 10rem;
                }
            }
        }
        .opciones{
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 1rem;
            @include m.tablet { 
                flex-direction: row;
                justify-content: space-around;
            }
            .submit-nueva-tarea{
                @include m.boton(v.$indigo, v.$blanco);
            }
            .cerrar-modal{
                @include m.boton(v.$rojo, v.$blanco); 
            }
        }
        legend{
            font-weight: 900;
            display: block;
            padding-bottom: 1rem;
        }
    }
}