@use 'base/mixins' as m;
@use 'base/variables' as v;
.contenedor-nueva-tarea{
    display: flex;
    justify-content: center;
    .agregar-tarea{
        @include m.boton(lighten(v.$cyan,5%),v.$blanco);
    }
}
.listado-tareas{
    padding-left: 0;
    list-style: none;
    .no-tareas{
        display: block;
        text-align: center;
    }
    .tarea{
        padding: 1rem;
        @include m.tablet { 
        padding: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        }
        border-bottom: 1px solid lighten(v.$gris, 30%);
       &:last-of-type{
            border-bottom: none;
        }
        .opciones{
            display: flex;
            gap: 1rem;
        }
        button{
            cursor: pointer;
            border: none;
            font-size: 1.4rem;
            padding: .5rem 1rem;
            color: v.$blanco;
            border-radius: 1rem;
            font-weight: 900;
            text-transform: uppercase;
        }
        .pendiente{
            background-color: v.$naranja;
        }
        .completa{
            background-color: v.$azul;
        }
        .eliminar-tarea{
            background-color: v.$rojo;
        }
        p{
            font-size: 1.6rem;
        }
    }
}