@use 'base/mixins' as m;
@use 'base/variables' as v;
.no-proyectos{
    text-align: center;
}
.proyectos{
    margin-top: 3rem;
    padding-left: 0;
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 2rem;
    }
    @include m.desktop { 
        grid-template-columns: repeat(3,1fr);
    }
    .proyecto{
        list-style: none;
        background-color: lighten(v.$indigo, 4%);
        padding: 2rem;
        border-radius: 1rem;
        margin-bottom: 2rem;
        line-height: 2;
        display: flex;
        align-items: center;
        justify-content: space-around;
        @include m.tablet { 
            margin-bottom: 0;
        }
        &:hover{
            background-color: darken(v.$indigo, 5%);
            cursor: pointer;
        }
    }
}