@use 'base/variables'as v;
.alertas{
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    font-weight: 900;
    margin-bottom: 1rem;
    font-size: 1.2rem;
    max-width: 80rem;
    margin-top: 2rem;
    &.error{
        background-color: lighten(v.$rojo, 55%);
        border-left: solid .5rem v.$rojo;
    }
    &.exito{
        background-color: lighten(v.$verde, 55%);
        border-left: solid .5rem v.$verde;
    }
}