@use 'base/mixins' as m;
@use 'base/variables' as v;
.barra-movile{
    background-color: v.$indigo;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    @include m.tablet { 
        display: none;
    }
    h1{
        color: v.$blanco;
        margin-bottom: 0;
    }
    .menu{
        img{
            width: 5rem;
            background-color: lighten(v.$indigo, 10%);
            box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.75);
            &:hover{
                cursor: pointer;
            }
        }
    }
}
    .barra{
        display: none;
        @include m.tablet { 
            margin: 0 auto;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 2rem;
            box-shadow: 1px 5px 10px -1px rgba(0, 0, 0, 0.205);    
        }
        span{
            color: v.$indigo;
            font-weight: 900;
        }
        .cerrar-sesion{
            color: v.$blanco;
            background-color: v.$indigo;
            padding: 1rem;
            border-radius: 1rem;
        }
    }
