@use 'base/mixins' as m;
@use 'base/variables' as v;
.dashbord{
    background-color: lighten(v.$grisClaro, 10%);
    
    @include m.tablet { 
        display: flex;
        height: 100vh;
    }
    .principal{
        flex: 1;
        overflow: scroll;
    }
    .contenido{
        padding: 2rem;
        .nombre-pagina{
            font-size: 2.8rem;
            text-align: center;
        }
        a{
            justify-content: center;
            color: v.$blanco;
            font-weight: 900;
            padding: 1rem;
            text-align: center;
        }
        p{
            cursor: pointer;
        }
    }
    .formulario{
        input:not([type="submit"]){
            background-color: v.$blanco;
        }
        input[type="submit"]{
            @include m.boton(v.$indigo, v.$blanco);
        }
    }
    .enlace{
        background-color: v.$indigo;
        color: v.$blanco;
        &:hover{
            background-color: darken(v.$indigo, 20%);
        }
    }
}