@use 'base/variables' as v;
@use 'base/mixins' as m;
@media(prefers-color-scheme: dark){
    $grisOscuro:#2C2C3B;
    body,
    .dashbord{
        background-color: $grisOscuro;
    }
    .tagline,
    .formulario label{
        color: v.$blanco;
    }
    .descripcion-pagina,
    .formulario .campo input,
    .alertas.error,
    .listado-tareas .tarea p{
        color: v.$grisClaro;
    }
    .formulario .campo input,
    .dashbord .formulario input:not([type="submit"]){
        background-color: darken(v.$grisOscuro, 10%);
        border-radius: 1rem;
        border: none;
    }
    //Acciones de formularios
    .login .acciones a,
    .listado-tareas .no-tareas{
        color: lighten(v.$cyan, 30%);
    }
    .crear .acciones a{
        color: lighten(v.$morado, 30%);
    }
    .olvide .acciones a{
        color: lighten(v.$rosa, 30%);
    }
    //Alertas
    .alertas.error{
        background-color: darken(v.$rojo, 20%);
    }
    //Barra movile
    .barra-movile{
        background-color: darken(v.$indigo, 20%);
    }
    //Nombre de encabedo dashbord
    .dashbord .contenido .nombre-pagina{
        color: lighten(v.$indigo, 40%);
    }
    //Sider
    .sidebar{
        @include m.tablet { 
            background-color: darken($grisOscuro, 5%);
        }
        .sidebar-nav a{
            &.activo,
            &:hover{
                background-color: v.$naranja;
            }
        }
    }
    
    //Filtros de tareas
    .filtros .filtros-inputs{
        background-color: lighten(v.$grisOscuro, 10%);
        box-shadow: 0px 0px 10px -4px rgba(119, 0, 255, 0.644);
        input[type="radio"]{
            accent-color: v.$naranja;
        }
    }
    .filtros-inputs label,
    .filtros .filtros-inputs h2{
        color: lighten(v.$blanco, 25%);
    }
    .listado-tareas .tarea{
        background-color: lighten($grisOscuro, 10%);
        border: none;
        margin-bottom: 1rem;
        padding: 1rem;
        border-radius: 1rem;
    }
    //Barra tablet
    .barra{
        p{
            color: v.$naranja;
        }
        span{
            color: v.$grisClaro;
        }
        .cerrar-sesion{
            background-color: v.$naranja;
            color: v.$blanco;
        }
    }
    //Modal
    .formulario.nueva-tarea{
        background-color:$grisOscuro;
        legend{
            color: v.$grisClaro;
        }
    }
}