@use 'base/mixins' as m;
@use 'base/variables' as v;

.sidebar{
    height: 0;
    opacity: 0;
    background-color: v.$indigo;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 500ms;
    @include m.tablet { 
        display: block;
        opacity: 1;
        height: 100%;
        overflow: visible;
        padding: 2rem;
    }
    &.mostrar{
        padding: 2rem;
        opacity: 1;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-color: rgb(0 0 0 /.95);
        @include m.tablet { 
            display: none;
        }
    }
    &.ocultar{
        opacity: 0;
    }
    .contenedor-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 2rem;
        h2{
            text-align: center;
            color: v.$blanco;
            margin: 1rem 0;
        }
        .cerrar-menu{
            @include m.tablet { 
                display: none;
            }
            img{
                padding: .5rem;
                border-radius: 50%;
                background-color: v.$blanco;
                width: 4rem;
                width: 4rem;
                &:hover{
                    cursor: pointer;
                }
            }
        }
    }
    .sidebar-nav{
        margin-top: v.$separacion;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        a{
            font-size: 2.4rem;
            text-align: center;
            color: v.$blanco;
            font-weight: 900;
            padding: 1rem;
            transition-property: background-color;
            transition-duration: 300ms;
            margin-bottom: 4rem;
            &:hover,
            &.activo{
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }
            @include m.tablet { 
                text-align: start;
                margin-bottom: .2rem;
                font-size: 1.8rem;
            }
        }
    }
    .cerrar-session-mobile{
        
        margin-top: 10rem;
        .cerrar-session{
            @include m.boton(v.$indigo,v.$blanco);
            @include m.tablet { 
               display: none;
            }
        }
    }
}