.dashbord {
  background-color: white;
}
@media (min-width: 768px) {
  .dashbord {
    display: flex;
    height: 100vh;
  }
}
.dashbord .principal {
  flex: 1;
  overflow: scroll;
}
.dashbord .contenido {
  padding: 2rem;
}
.dashbord .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}
.dashbord .contenido a {
  justify-content: center;
  color: #FFFFFF;
  font-weight: 900;
  padding: 1rem;
  text-align: center;
}
.dashbord .contenido p {
  cursor: pointer;
}
.dashbord .formulario input:not([type=submit]) {
  background-color: #FFFFFF;
}
.dashbord .formulario input[type=submit] {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
