.sidebar {
  height: 0;
  opacity: 0;
  background-color: #4338CA;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
@media (min-width: 768px) {
  .sidebar {
    display: block;
    opacity: 1;
    height: 100%;
    overflow: visible;
    padding: 2rem;
  }
}
.sidebar.mostrar {
  padding: 2rem;
  opacity: 1;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.95);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}
.sidebar .contenedor-sidebar h2 {
  text-align: center;
  color: #FFFFFF;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .contenedor-sidebar .cerrar-menu img {
  padding: 0.5rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  width: 4rem;
  width: 4rem;
}
.sidebar .contenedor-sidebar .cerrar-menu img:hover {
  cursor: pointer;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.sidebar .sidebar-nav a {
  font-size: 2.4rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 900;
  padding: 1rem;
  transition-property: background-color;
  transition-duration: 300ms;
  margin-bottom: 4rem;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #28217b;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    text-align: start;
    margin-bottom: 0.2rem;
    font-size: 1.8rem;
  }
}
.sidebar .cerrar-session-mobile {
  margin-top: 10rem;
}
.sidebar .cerrar-session-mobile .cerrar-session {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .sidebar .cerrar-session-mobile .cerrar-session {
    width: auto;
  }
}
.sidebar .cerrar-session-mobile .cerrar-session:hover {
  background-color: #352ca3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .cerrar-session-mobile .cerrar-session {
    display: none;
  }
}

.barra-movile {
  background-color: #4338CA;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
}
@media (min-width: 768px) {
  .barra-movile {
    display: none;
  }
}
.barra-movile h1 {
  color: #FFFFFF;
  margin-bottom: 0;
}
.barra-movile .menu img {
  width: 5rem;
  background-color: #6960d5;
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
}
.barra-movile .menu img:hover {
  cursor: pointer;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2rem;
    box-shadow: 1px 5px 10px -1px rgba(0, 0, 0, 0.205);
  }
}
.barra span {
  color: #4338CA;
