  padding: 1rem;
  border-radius: 1rem;
}

.no-proyectos {
  text-align: center;
}

.proyectos {
  margin-top: 3rem;
  padding-left: 0;
}
@media (min-width: 768px) {
  .proyectos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .proyectos {
    grid-template-columns: repeat(3, 1fr);
  }
}
.proyectos .proyecto {
  list-style: none;
  background-color: #5248ce;
  padding: 2rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
  line-height: 2;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .proyectos .proyecto {
    margin-bottom: 0;
  }
}
.proyectos .proyecto:hover {
  background-color: #3b31b8;
  cursor: pointer;
}

