.modal {
  position: absolute;
  background-color: rgba(44, 61, 207, 0.452);
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.modal .formulario {
  max-width: 90%;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 500ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
}
.modal .formulario.animacion {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
@media (min-width: 768px) {
  .modal .formulario {
    max-width: 60rem;
  }
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}
