@use 'base/mixins' as m;
@use 'base/variables' as v;

.filtros{
    margin-top: 2rem;
    .filtros-inputs{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        @include m.tablet { 
            flex-direction: row;
            gap: 3rem;
        }
        box-shadow: 0px 0px 10px -4px rgba(0,0,0,0.75);
        padding: 2rem;
        border-radius: 1rem;
        background-color: lighten(v.$gris, 50%);
        h2{
            margin-bottom: 0;
            font-size: 2.8rem;
            color: v.$indigo;
        }
        label{
            font-weight: 700;
        }
        input[type="radio"]{
            accent-color:v.$indigo;
        }
    }
    .campo{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .5rem;
        @include m.tablet { 
            justify-content: stretch;
            gap: 1rem;
        }
    }
}